import React, { Component } from "react";
import "./App.css";
import share from "./share.svg";
import axios from "axios";

import {
  FacebookShareButton,
  FacebookIcon,
  WhatsappShareButton,
  WhatsappIcon,
  TwitterShareButton,
  TwitterIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "react-share";

class Cards extends Component {

  constructor(){
    super()
    this.state = {
      cData:[]
    }
  }
  

  componentDidMount(){
    fetch("/data")
    .then(res=> res.json())
    .then((r)=> {
      let ff = r.map((d)=>{
        return d
      })
      // console.log(ff);
      this.setState({
        cData: ff,
      })
  } )
  }

  postData(index){
    let ff = document.querySelectorAll('.ccc')

    const clickedNode = ff[index];

    const clickedNodeTextContent = clickedNode.textContent; 
   
    let data = { course: clickedNodeTextContent}

    axios.post('/api', data)
    .then(res => {
      console.log(res);
    })
    .catch(err =>{
      console.log(err);
    })
  }

  dropDetails(index) {
    let details = document.querySelectorAll(".drop");

    let detail = details[index]

    detail.style.display = detail.style.display === "none" ? "block" : "none";
    // if (detail.style.display === "none") {
    //   detail.style.display = "block";
    // } else {
    //   detail.style.display = "none";
    // }
  }

  shareCard(index) {
    let btns = document.querySelectorAll(".share-btn");
    let sbtns = document.querySelectorAll(".sbtn");

    let btn = btns[index]
    let sbtn = sbtns[index] 

    btn.style.display = "block";
    sbtn.style.display = "none";
  }

  render() {
    return (
      <div className="text-center">
      { this.state.cData.map((el, index) => (
      <div className="card mb-3 jj" key={index}>
       
        <div className="row g-0">
          <div className="col-md-4">
            
            <img
              src={el.Image}
              alt="..."
              className="img-fluid ii"
            />
            <div className="but mt-2">
            <button
              type="button"
              className="btn-sm btn-info btt"
              onClick={() => this.dropDetails(index)}
            >
              <small>More Details </small>
            </button>
            <a href="/form" target="_blank" rel="norefferer" >
            <button type="button" className="btn-sm btn-info" onClick={() => this.postData(index)}>
              <small> Book Now </small>
            </button>
            </a>
          </div>
          </div>

          <div className="col-md-6">
            <div className="card-body">
              <h5 className="card-title ccc">{el.Course}</h5>
              <b> {el.FFS} </b> <br />
              <span className="card-text">
                <small className="text-muted">{el.FPS}</small>
              </span>
              <br /> <br />
              <span>
                By : {el.CP} <br />
                Age : {el.Age} <br />
                {el.TIW} <br />
                {el.ON} | {el.Cen} <br />
                {el.Skills}
              </span>{" "}
              <br /> <br />
              <div className="share-btn">
                <FacebookShareButton
                  url={"https://curioboat.com/sports-and-fitness"}
                  quote={"Kam bol be"}
                >
                  <FacebookIcon size={30} round={true}/>
                </FacebookShareButton>
                <WhatsappShareButton
                  url={"https://curioboat.com/"}
                  title={"Shubham is great"}
                >
                  <WhatsappIcon size={30} />
                </WhatsappShareButton>
                <TwitterShareButton url={"https:/spaceboat.in"}>
                  <TwitterIcon size={30} />
                </TwitterShareButton>
                <LinkedinShareButton url={"https://react-cards-demo.herokuapp.com/card"}>
                  <LinkedinIcon size={30} />
                </LinkedinShareButton>
              </div>
            </div>
          </div>
          <div className="col-md-1 share">
              <button onClick={() => this.shareCard(index)} className="sbtn">
              <img src={share} width="20px" height="20px" alt="share button" />
            </button>
          </div>
        </div>
        <div className="drop">
          <div className="accordion" id="accordionExample">
            <div className="accordion-item">
              <h2 className="accordion-header" id="headingOne">
                <button
                  className="accordion-button collapsed"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#collapseOne"
                  aria-expanded="false"
                  aria-controls="collapseOne"
                >
                  {el.MDT1}
                </button>
              </h2>
              <div
                id="collapseOne"
                className="accordion-collapse collapse"
                aria-labelledby="headingOne"
                data-bs-parent="#accordionExample"
              >
                <div className="accordion-body">
                 {el.MDD1}
                </div>
              </div>
            </div>
            <div className="accordion-item">
              <h2 className="accordion-header" id="headingTwo">
                <button
                  className="accordion-button collapsed"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#collapseTwo"
                  aria-expanded="false"
                  aria-controls="collapseTwo"
                >
                  {el.MDT2}
                </button>
              </h2>
              <div
                id="collapseTwo"
                className="accordion-collapse collapse"
                aria-labelledby="headingTwo"
                data-bs-parent="#accordionExample"
              >
                <div className="accordion-body">
                  {el.MDD2}.
                </div>
              </div>
            </div>
          </div>
          <div className="text-center">
            <button className="btn btn-danger btn-sm bb" type="button">
              Book Trial Class
            </button>
          </div>
        </div>
      </div>
    
     ) )}
     </div>
     );
  }
}

export default Cards;
