import React,{Component} from 'react';
import axios from 'axios'
import './Form.css'

class Form extends Component{
  state = {  
    prog:[],
    tData:[],
    name:'',
    age:'',
    email:'',
    tc:''
  }

  componentDidMount(){
    let url = '/api'
    fetch(url)
    .then(res => res.json())
    .then(
      (result) => {
        let dd = result.map((r) =>{
          return r.course 
        })
      let gg = dd.length
      let ggi = gg-1;
      let i;

      fetch('/data')
      .then(res2=>res2.json())
      .then(
        (rr) =>{
          rr.map((oo) => {
            if(oo.Course === dd[ggi]){
  
              i = rr.indexOf(oo)
              return i
            }
            else {
              return null
            }
  
          })
          let jj = [rr[i].TC1, rr[i].TC2]
           this.setState({
            tData: jj,
            program: dd[ggi]
            }) 
        })
        })
  }


handleNameChange = (event) =>{
  this.setState({
    name: event.target.value
  })
}

handleAgeChange = (event) =>{
  this.setState({
    age: event.target.value
  })
}

handleEmailChange = (event)=>{
  this.setState({
    email: event.target.value
  })
}

handleTrialChange = (event) =>{
  console.log(event.target.textContent);
  this.setState({
    tc: event.target.textContent 
  })
}

handleSubmit = (event) =>{
  event.preventDefault();
  let data = {name : this.state.name, 
              age:   this.state.age, 
              email: this.state.email, 
              program: this.state.program,
              tc:this.state.tc}
  axios.post('/fd', data)
  .then(response => {
    console.log(response);
  })
  .catch(error=> {
    console.log(error);
  })

 alert('Form is submitted, Thank you!!');

  window.location.reload();
}

  render() {
    return (
      <div className="main container">
       <div className="offset-md-3">
          <h2> Curio Trial Class Form</h2>
      <form onSubmit={this.handleSubmit}>
        <div className="form-group col-md-8">
          <label htmlFor="exampleInputEmail1">Name</label>
          <input type="text" required className="form-control" value={this.state.name} onChange={this.handleNameChange} placeholder="Enter Name" />
        </div>
        <div className="form-group col-md-8">
          <label htmlFor="exampleInputEmail1">Age</label>
          <input type="number" required className="form-control" value={this.state.age} onChange={this.handleAgeChange} placeholder="Enter Age" />
        </div>

        <div className="form-group col-md-8 mt-2">
          <p> Trial Classes:</p>
        <div className="btn-group btn-group-toggle optc col-md-9" data-toggle="buttons">
          {this.state.tData.map(el =>(
          <label className="btn btn-outline-danger opt" key={Math.random()} onClick={this.handleTrialChange}>
            <input type="radio" name="options" id="option1" autocomplete="off" key={Math.random()} />
            {el} 
          </label>
          ))}
        </div> 
        </div>
        
        <div className="form-group col-md-8">
          <label htmlFor="exampleInputEmail1">Email address</label>
          <input type="email" required className="form-control" value={this.state.email} onChange={this.handleEmailChange} placeholder="Enter email" />
          <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
        </div>

        <div className="form-group col-md-8 ml-5">
        <button type="submit" className="btn btn-primary">Submit</button>
        </div>
      </form>
      </div>
      </div>
          );
  }
}

export default Form;