import React from 'react';
import {Helmet} from "react-helmet"


function Card1() {
    let image = "https://image.shutterstock.com/image-photo/bright-spring-view-cameo-island-260nw-1048185397.jpg";
return (
    <React.Fragment>
    <Helmet>
        <title> 
            Course Card - Card 1
        </title>
        <meta http-equiv="X-UA-Compatible" content="IE=edge" />
        <meta name="csrf_token" content="" />
        <meta property="type" content="website" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no"/>
        <meta property="og:locale" content="en_IN" />
        <meta property="image" content={image} />
        <meta name="description" content="This is the content for Card1" />
        <meta property="og:image" content={image} />
        <meta content="image/*" property="og:image:type" />
        <meta property="og:site_name" content="ReactCards" />
        <meta property="og:description" content="This is the content for Card1" />  
        <meta property="og:title" content="Course Card - Card 1" />  
    </Helmet>
        <h2 className="text-center"> Here you will find content for Card1</h2>
    </React.Fragment>
)
}

export default Card1;